<template>
  <div class = "container-fluid">
    <h1 class="mt-5">Latest Posts</h1>
    <!-- CREATE POST -->
    <div class="container-fluid ">
      <div class="row">
        <div class="col-md-1"></div>
        <!--Text label-->
        <div class="col-md-2 mt-1">
          <label for="create-post" class="form-label">Create a post!</label>
        </div>
        <!--Input-->
        <div class="col-md-6 mb-2">
          <input type="text" class="form-control " id="create-post" v-model="text" placeholder="Say something here ...">
        </div>
        <!--Button-->
        <div class="col-md-1">
          <button v-if="(error || loadingPosts)" disabled class="btn btn-dark " v-on:click="createPost">Post!</button>
          <button v-else class="btn btn-dark " v-on:click="createPost">Post!</button>
        </div>
        <div class="col-md-1"></div>
      </div>
      
      <!--Updating posts message-->
      <div class="row" v-if="showUpdatingPostMessage">
        <div class="col-md-3"></div>
        <div class="form-text">
          <div class="spinner-border spinner-border-sm" role="status">
            <span class="sr-only"></span>
          </div>
          Updating posts...
        </div>
      </div>
    </div>

    <hr/>
    
    <!--Error message-->
    <p class="text-danger" v-if="error">{{error}}</p>

    <!--Loading posts -->
    <div v-else-if="loadingPosts">
      <div class="spinner-border spinner-border-sm" role="status">
        <span class="sr-only"></span>
      </div>
      Loading posts ...
    </div>

    <!--No posts message-->
    <div v-if="(!loadingPosts && posts.length == 0)">
      <p class="text-muted">No posts yet!</p>
    </div>

    <!--POSTS -->
    <div class="container">
      <div class="post"
        v-for="(post, index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post._id"
        >
        <div class="card text-center mb-2">
          <button @click="deletePost(post._id)" class="btn btn-outline-secondary border-0 ltm-delete-post">
                  <i class="fa-solid fa-x"></i>
            </button> 
          <div class="card-body">
            <p class="text h4">{{post.text}}</p>
          </div>
          <div class="card-footer text-muted">
            <!--Created at-->
            {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}
            
          </div>
        </div>
        
      </div>
    </div>
        
  </div>
</template>

<script>
import PostService from "../PostService"

export default {
  name: 'PostComponent',
  data(){
    return {
      posts: [], // Stores all the posts to display
      error: '', // A string to report an error message
      text: '', // The text to add to the post
      loadingPosts: true,
      showUpdatingPostMessage: false, // True when waiting for posts to update
    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts()
      this.loadingPosts = false
    } catch(err) {
      this.error = err.message
    }
  },
  methods: {
    async createPost(){
      this.showUpdatingPostMessage = true;
      await PostService.insertPost(this.text)
      this.posts = await PostService.getPosts()
      this.showUpdatingPostMessage = false;
      this.text = ''
    },
    async deletePost(id){
      await PostService.deletePost(id)
      this.posts = await PostService.getPosts()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.ltm-delete-post{
  position: absolute;
  top:5px;
  right: 5px;
}
</style>
